body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  color: #373a3c;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

a:focus {
  text-decoration: underline;
}

/* 
容器
1. 居中
2. max-width 的 内边距 
*/
.container {
  margin: 0 auto;
  padding: 0 15px;
}



